import { Home, User, Settings, LogOut } from "lucide-react";
import Link from "next/link";
import { useRouter } from "next/router";

const menuItems = [
  { label: "Início", icon: Home, href: "/" },
  { label: "Usuários", icon: User, href: "/usuarios" },
  { label: "Configurações", icon: Settings, href: "/config" },
];

export default function SideMenu() {
  const router = useRouter();

  return (
    <aside className="h-screen w-64 bg-gray-900 text-white flex flex-col shadow-lg fixed">
      <div className="px-6 py-4 text-2xl font-bold border-b border-gray-700">
        Meu Painel
      </div>

      <nav className="flex-1 px-2 py-4 space-y-2">
        {menuItems.map(({ label, icon: Icon, href }) => {
          const isActive = router.pathname === href;
          return (
            <Link
              key={href}
              href={href}
              className={`flex items-center px-4 py-2 rounded-lg transition-colors duration-200
                ${isActive ? "bg-gray-700" : "hover:bg-gray-800"}`}
            >
              <Icon className="w-5 h-5 mr-3" />
              <span className="text-sm font-medium">{label}</span>
            </Link>
          );
        })}
      </nav>

      <div className="px-4 py-3 border-t border-gray-700">
        <button className="flex items-center w-full text-left text-red-400 hover:text-red-300 transition">
          <LogOut className="w-5 h-5 mr-2" />
          Sair
        </button>
      </div>
    </aside>
  );
}
